<template>
    <div style="width: 100%;height: 420px;" id="yixiao">

    </div>
</template>
<script>
export default {
    mounted() {
        this.yx()
    },
    methods: {
        yx() {
            let myChart = this.$echarts.init(document.getElementById('yixiao'))
            // 绘制图表
            myChart.setOption({
                tooltip: {
                    trigger: 'axis'
                },
                legend: {
                    data: ['通话并发数', '线路并发数'],
                    bottom: 0
                },
                xAxis: {
                    boundaryGap: false,
                    data: ['1.00', '2.00', '3.00', '4.00', '5.00', '6.00', '7.00', '8.00', '9.00', '10.00', '11.00', '12.00'],
                    name: '时间'
                },
                yAxis: {
                    name: '并发数',
                    type: 'value',

                },
                series: [
                    {
                        name: '通话并发数',
                        type: 'line',
                        data: [0, 0, 0, 6, 6, 0, 0, 1, 2, 0, 0, 0]
                    },
                    {
                        name: '线路并发数',
                        type: 'line',
                        data: [0, 0, 0, 7, 6, 0, 0, 3, 2, 4, 0, 0]
                    },
                ]
            });
        }
    }
}
</script>